import React from 'react'

function Privacy() {
    const domainlog = window.location.origin
    return (
        <div style={{padding:'0px 53px', height: '100vh', background: 'white'}}>
            <h1 style={{padding:'14px 0px', color:'black', textAlign:'center', textDecoration: 'underline'}}>Privacy Policy</h1> <br /> <br />
            <li>Cookies are being used to improve the software</li><br />
            <li>Cookies will not be shared with any third agency</li><br />
            <li>Cookies dont contain any personal details like Contact info or Account </li><br />
            <li style={{fontWeight:'bold', color:'tomato'}}>*Note* Deletion of cookies may result in website crash, if this occurs go to homepage - <a href={domainlog}>{domainlog}</a></li><br />
             
        </div>
    )
}

export default Privacy
