import React from 'react'
import "./category.css"
import firebase from 'firebase'
import Cookies from 'js-cookie'
import firebaseConfig from '../DataBase/config'
import dogimage from './icons/dog.jpg'
import catimage from './icons/cat.jpg'
import birdimage from './icons/bird.jpg'
import fishimage from './icons/fish.jpg'
function Category() {
    let products  = []
    if (!firebase.apps.length) {
        firebase.initializeApp(firebaseConfig);
    }
    let ref = firebase.database().ref('/livestocks/');
    ref.on('value', snapshot => {
    products = snapshot.val()
    console.log(products)
    Cookies.set('livestock', products)});
    return (
        <div className="category">
            <a href="/store/fishes"><img src={fishimage} ></img></a>
            <a href="/store/birds"><img src={birdimage} /*style={{position: 'relative', top: '-4px'}}*/></img></a>
            <a href="/store/cats"><img src={catimage} /*style={{position: 'relative', top: '-7px'}}*/></img></a>
            <a href="/store/dogs"><img src={dogimage} /*style={{position: 'relative', top: '-10px'}}*/></img></a>
        </div>
    )
}

export default Category
