import React, {useState} from 'react'
import "./Fish-page.css"
import firebase from 'firebase'
import Cookies from 'js-cookie'
import firebaseConfig from '../DataBase/config'
import Product from "../Component/product"
import IconButton from '@material-ui/core/IconButton';
import {Button} from '@material-ui/core'
import ShoppingCartIcon from '@material-ui/icons/ShoppingCart';
import Badge from '@material-ui/core/Badge'
export default function Fish_Page() {
  const [numitems, setnumitems] = useState()
  const livestock = JSON.parse(Cookies.get('livestock'))
  const cart = JSON.parse(Cookies.get('cart'));
  const load=()=>{
    if (cart && cart.length){
      let a = JSON.parse(Cookies.get('cart'));
      setnumitems(a[0].length)
    }else{ 
      let a = [[0]]
      setnumitems(a[0].length)
    }
  }
    let products  = []
    if (!firebase.apps.length) {
        firebase.initializeApp(firebaseConfig);
    }
    let ref = firebase.database().ref('/livestocks/');
    ref.on('value', snapshot => {
    products = snapshot.val()
    console.log(products)
    Cookies.set('livestock', products)});
    return (
        <div className="Fish-page" onLoad={load}>
          <h1 className="fishes" style={{width: '384px'}}>Fishes</h1>
            {livestock.fish.map(num=>(<Product 
            
            img = {num[0]}
            name={num[1]}
            description="The Siamese fighting fish (Betta splendens), commonly known as the betta, is a freshwater fish found in Cambodia, Laos, Malaysia, Indonesia, Thailand, and Vietnam."
            cost={num[2]} 
             />))}         
        </div>
    )
}
 