import React, {useState, useEffect} from 'react'
import firebase from 'firebase'
import firebaseConfig from '../DataBase/config'
import Item from './item'
import {Button} from '@material-ui/core'
function Pets() {
    const [products, setproducts] = useState([])
    const [a, sa] = useState([])
    const [text, settextto] = useState('Click here to Load available products')
    const b = []
    var num = []
    
    async function Loader(){
        
        if(products && products.length){
            if(products && products.length){   
                for (let i = 0; i < products.length; i++) {
                    num.push(i)
                }
            }      
            sa(num.map( digit => (
                <div>
                <Item product_img={products[digit][0]} 
                product_name={products[digit][1]}            
                product_cost={products[digit][2]} />
                <br></br>
                </div>)))
            settextto("Pet Supplements")
         }else{ console.log("wont run")}
    }
     
    useEffect(()=>{
        console.log("onMount")
        if (!firebase.apps.length) {
            firebase.initializeApp(firebaseConfig);
        }
        let ref = firebase.database().ref('/petsupplementpage/');
        ref.on('value', snapshot => {
        setproducts(snapshot.val())
        console.log(products)
        if(products && products.length){   
            Loader();
        }
        
    });
    }, [])
    return (
        <div onLoad={Loader}>
            <Button variant="contained" color="secondary" id="Load-btn" style={{width: '221px'}} onClick={Loader}>{text}</Button>
            {Loader}
            <br></br>
            {a}
        </div>
    )
}

export default Pets
