import React, {useState} from 'react'
import Paper from '@material-ui/core/Paper'
import TextField from '@material-ui/core/TextField';
import MenuItem from '@material-ui/core/MenuItem';
import Cookies from 'js-cookie'
import DeleteIcon from '@material-ui/icons/Delete';
import IconButton from '@material-ui/core/IconButton';
import {Button }from '@material-ui/core/';

function Checkout() {
    const [Cart, setcart] = useState(JSON.parse(Cookies.get('cart')))
    const [quant, setquant] = useState(1)
    let costdel = 0
    costdel = Cart[0].length * 50
    console.log('a', Cart)
    const [times, settimes] = React.useState([])
    if (Cart[0].length===0){
        window.location.href="/store"
        }
    for (let i = 0; i < Cart[0].length; i++) { 
        console.log(Cart[0].length)
        times.push(i)
      } 
      let a = 0
      let b = 0
      for (let i = 0; i < Cart[3].length; i++) { 
        b = a + Cart[3][i]
        a = b
      }   
    const bn =  b
    b = b+costdel
    Cookies.set('amnt', bn + costdel)
    costdel = '₹'+costdel
    
    
    const Checkoutitems =({img, name, cost})=>{
        const remove = () => {
            console.log(Cart);
            const index = Cart[0].indexOf(img);
            console.log(Cart[1][index]);
            Cart[0].splice(index, 1)
            Cart[1].splice(index, 1)
            Cart[2].splice(index, 1)
            Cart[3].splice(index, 1)
            Cookies.set('cart', Cart)
            console.log(Cart)
            window.location.href ="/store/checkout"
        }
        
        return(
            <div className="checkoutitem" style={{display: 'flex', fontFamily: 'PLATFORM'}}>
                <img src={img} style={{width : '30%'}}></img>
                <div className="checkoutitem-details">
                    <h3>{name}</h3>
                    <h4>{cost}</h4>
                    <select id="items">
                        <option value="1">1</option>
                        <option value="2">2</option>
                        <option value="3">3</option>
                        <option value="4">4</option>
                    </select>
                    <IconButton color="inherit" onClick={remove}>
                        <DeleteIcon />
                    </IconButton>
                </div>
                
            </div>
        )
    }
    
    console.log('send')
     
    return (
        <div className="checkout-page">
            <Paper elevation={5} style={{width: '98%', display: 'grid', marginLeft: 'auto', marginRight: 'auto', background: '#eaeded'}}>
                <h1 style={{textAlign: 'center', fontFamily: 'PLATFORM'}}>Checkout Total : ₹{b}</h1>
                <br></br><br></br>
                {times.map(d =>(
                    <div>
                    <Checkoutitems img={Cart[0][d]}
                    name = {Cart[1][d]}
                    cost = {Cart[2][d]} />
                    <br></br><br></br>
                    </div>
                ))}
                <div className="checkoutitem" style={{display: 'flex'}}>
                <img id="cartproductimg" src="https://img.etimg.com/thumb/width-1200,height-900,imgsize-137222,resizemode-1,msid-71401965/industry/miscellaneous/geopost-in-talks-to-pick-up-20-25-more-in-dtdc.jpg" style={{width : '30%', paddingRight: '8px'}}></img>
                <div className="checkoutitem-details" >
                    <h3>"Deliver Charges ₹50.00 for per item"</h3>
                    <h4>{costdel}</h4>
                    <select id="items">
                        <option value="1">1</option>
                        <option value="2">2</option>
                        <option value="3">3</option>
                        <option value="4">4</option>
                    </select>
                </div>
                </div> <br />
                <Button variant="contained" style={{ background: 'linear-gradient(85deg, rgb(63, 81, 244) , rgb(107, 145, 253))',
                        width:' 267px',
                        marginLeft:' auto',
                        marginRight: 'auto',
                        color: 'white',
                        fontFamily: 'uni sans'}}
                        href="/store/checkout/payment"
                        >continue to payment page</Button>
                <br></br>
            </Paper>
             
        </div>
    )
}

export default Checkout
