import React from 'react'

function refund() {
    const domainlog = window.location.origin
    return (
        <div style={{padding:'0px 53px', height: '100vh', background: 'white'}}>
            <h1 style={{padding:'14px 0px', color:'black', textAlign:'center', textDecoration: 'underline'}}>Return and Refund</h1> <br /> <br />
            <li>Once the order is placed, it cannot be cancelled/exchanged/returned</li><br />
            <li>If the order cannot be fulfilled due to unavailability of the item or any other delays, we can cancel the order and issue a <u>refund</u> (within 7-15 working days exluding sundays and holidays)</li><br />
            <li>If the item has not left the warehouse (i.e 2-3 hours after payment) cancellation of order and refund can be issued by contacting the seller here - {domainlog}/store/contact</li><br />
        </div>
    )
}

export default refund
