import React, {useContext, useState} from 'react'
import Paper from '@material-ui/core/Paper'
import Context from '../store/context'
import IconButton from '@material-ui/core/IconButton';
import Snackbar from '@material-ui/core/Snackbar';
import MuiAlert from '@material-ui/lab/Alert';
import './item.css'
import {Backdrop} from '@material-ui/core'
import InfoIcon from '@material-ui/icons/Info';
import Cookies from 'js-cookie'
import {Button} from '@material-ui/core'
import AddIcon from '@material-ui/icons/AddShoppingCart';
import { makeStyles } from '@material-ui/core/styles';

 
function Item({product_img, product_name, product_cost}) {
    var concat = ("₹" + product_cost.toString())
    const {state, actions} = useContext(Context)
    const [info, setinfo] = useState(false)
    const [open, setOpen] = useState(false)
    var cart = JSON.parse(Cookies.get('cart'))
    function Alert(props) {
        return <MuiAlert elevation={6} variant="filled" {...props} />;
      }
    const handleClose = (event, reason) => {
        if (reason === 'clickaway') {
          return;
        }
        setOpen(false)
    }
    const buy=()=>{
        Cookies.set('cart', [[product_img], [product_name], [concat], [product_cost]])
        setTimeout(()=>{window.location.href="/store/checkout"}, 1000)
    }
    const addtocart =(error)=>{
        console.log(error)
        actions({type:'setState', payload : {...state, list: [state.cartitems[0] = state.cartitems[0] +1]}})
        actions({type:'setState', payload : {...state, list: [state.items[0].push(product_img)]}})
        actions({type:'setState', payload : {...state, list: [state.items[1].push(product_name)]}})
        actions({type:'setState', payload : {...state, list: [state.items[2].push(concat)]}})
        actions({type:'setState', payload : {...state, list: [state.items[3].push(product_cost)]}})
        setOpen(true);
        if (cart && cart.length){
            cart[0].push(product_img)
            cart[1].push(product_name)
            cart[2].push(concat)
            cart[3].push(product_cost)
            Cookies.set('cart', cart)
            console.log('method1')
        }
        else{
            Cookies.set('cart', state.items)
        }
        console.log(state.items);
        Cookies.set('item', state.cartitems[0])
        
    }
    const useStyles = makeStyles((theme) => ({
        button: {
          margin: theme.spacing(1),
        },
        backdrop: {
            zIndex: theme.zIndex.drawer + 1,
            color: '#fff',
          },
      }));
      const classes = useStyles();
    return (
        <div className="item-component">
            <Paper elevation={3} id="item-paper">
                <div id="item">
                    <img src={product_img}></img>
                    <div className="written-details">
                        <h3>{product_name}</h3>
                        <h4 id="cost">₹{product_cost}<IconButton onClick={()=>{setinfo(true)}}><InfoIcon /></IconButton></h4>
                        <div className="add-to-cart">
                        <Button
                        variant="contained"
                        onClick={addtocart}
                        color="secondary"
                        className={classes.button}
                        startIcon={<AddIcon />}
                        style={{position: 'relative', right: '10%', display: 'flex', width: '168px'}}>
                        Add to Cart </Button>
                        <Button
                        variant="contained"
                        onClick={buy}
                        color="secondary"
                        className={classes.button}
                        startIcon={<AddIcon />}
                        style={{position: 'relative', right: '10%', display: 'flex', width: '168px'}}>
                        Buy Now </Button>
                         
                        </div>
                        
                    </div>
                </div>
            </Paper>
            <Backdrop className={classes.backdrop} open={info} onClick={()=>{setinfo(false)}} >
            <Paper style={{width: '90vw', height: '50vh'}}><div style={{display: 'flex'}}><img style={{width:'30%'}} src={product_img} className="pimage-bd"/>
            <div><h2 style={{fontSize: '100%', padding: '12px 1px', fontFamily: 'proxima'}}>{product_name}</h2>
            <h3 style={{fontSize: '100%', padding: '12px 1px', fontFamily: 'PLATFORM'}}>₹{product_cost}</h3></div></div>
            <div style={{display: 'flex'}}>
             g
            <Button
            variant="contained"
            onClick={buy}
            color="secondary"
            className={classes.button}
            startIcon={<AddIcon />}
            style={{position: 'relative', right: '10%', display: 'flex', width: '131px', fontSize: '14px', height: '40px'}}>
            Buy Now </Button>
            </div>     
            </Paper>
        </Backdrop>
            <Snackbar open={open} autoHideDuration={2000} onClose={handleClose}>
            <Alert onClose={handleClose} severity="success">
             Item Added to cart
            </Alert>
        </Snackbar>
        
        </div>
    )
}

export default Item
