import React, {useState} from 'react'
import Paper from '@material-ui/core/Paper'
import TextField from '@material-ui/core/TextField';
import MenuItem from '@material-ui/core/MenuItem';
import Cookies from 'js-cookie'
import DeleteIcon from '@material-ui/icons/Delete';
import IconButton from '@material-ui/core/IconButton';
import {Button }from '@material-ui/core/';
function Shoppingcart() {
    const [Cart, setcart] = useState(JSON.parse(Cookies.get('cart')))
    console.log('a', Cart)
    const [times, settimes] = React.useState([])
    
    for (let i = 0; i < Cart[0].length; i++) { 
        console.log(Cart[0].length)
        times.push(i)
      } 
      let a = 0
      let b = 0
      for (let i = 0; i < Cart[3].length; i++) { 
        b = a + Cart[3][i]
        a = b
      }   
    const bn =  b
    Cookies.set('amnt', bn)
    b = "₹" + b.toString()
    const currencies = [
    {
        value: 1,
        label: '1',
    },
    {
        value: 2,
        label: '2',
    },
    {
        value: 3,
        label: '3',
    },
    {
        value: 4,
        label: '4',
    },
    ];
    
    
    const [currency, setCurrency] = React.useState(1);
    const handleChange = (event) => {
        setCurrency(event.target.value);
    };
    const Checkoutitems =({img, name, cost})=>{
        const remove = () => {
            console.log(Cart);

            const index = Cart[0].indexOf(img);
            console.log(Cart[1][index]);
            Cart[0].splice(index, 1)
            Cart[1].splice(index, 1)
            Cart[2].splice(index, 1)
            Cart[3].splice(index, 1)
            Cookies.set('cart', Cart)
            console.log(Cart)
            window.location.href ="/store/checkout"
            
        }
        
        return(
            <div className="checkoutitem" style={{display: 'flex'}}>
                <img src={img} style={{width : '30%'}}></img>
                <div className="checkoutitem-details" >
                    <h3>{name}</h3>
                    <h4>{cost}</h4>
                    <TextField
                    id="standard-select-currency"
                    select
                    value={currency}
                    onChange={handleChange}
                    helperText="Set Quantity"
                    >
                    {currencies.map((option) => (
                        <MenuItem key={option.value} value={option.value}>
                        {option.label}
                        </MenuItem>
                    ))}
                    </TextField>
                    <IconButton color="inherit" onClick={remove}>
                        <DeleteIcon />
                    </IconButton>
                </div>
                
            </div>
        )
    }
    
    console.log('send')
     
    return (
        <div className="checkout-page">
            <Paper elevation={3} style={{width: '98%', display: 'grid', marginLeft: 'auto', marginRight: 'auto', background:'#eaeded'}}>
                <h1 style={{textAlign: 'center'}}>Cart Total : {b}</h1>
                <br></br><br></br>
                {times.map(d =>(
                    <div>
                    <Checkoutitems img={Cart[0][d]}
                    name = {Cart[1][d]}
                    cost = {Cart[2][d]} />
                    <br></br><br></br>
                    </div>
                ))}
                <Button variant="contained" color="secondary" style={{ 
                        width:' 267px',
                        marginLeft:' auto',
                        marginRight: 'auto',
                        color: 'white',
                        fontFamily: 'uni sans'}}
                        href="/store/checkout"
                        >Proceed to checkout</Button>
                <br></br>
            </Paper>
        </div>
    )
}
export default Shoppingcart
