import React from 'react'
import Product from '../Component/product'
import firebase from 'firebase'
import Cookies from 'js-cookie'
import firebaseConfig from '../DataBase/config'
function Cat_Page() {
    const livestock = JSON.parse(Cookies.get('livestock'))
    let products  = []
    if (!firebase.apps.length) {
        firebase.initializeApp(firebaseConfig);
    }
    let ref = firebase.database().ref('/livestocks/');
    ref.on('value', snapshot => {
    products = snapshot.val()
    console.log(products)
    Cookies.set('livestock', products)});
    return (
        <div className="bird-page">
            <h1 className="birds">Cat</h1>
            {livestock.cat.map(num=>(<Product 
            
            img = {num[0]}
            name={num[1]}
            description="The Siamese fighting fish (Betta splendens), commonly known as the betta, is a freshwater fish found in Cambodia, Laos, Malaysia, Indonesia, Thailand, and Vietnam."
            cost={num[2]} 
             />))}   
        </div>
    )
}

export default Cat_Page
